@import url("color-palette.less");
@import 'https://fonts.googleapis.com/css?family=Lato:300,400|Open+Sans+Condensed:300|Open+Sans:300i,400,600';

/*
font-family: 'Lato', sans-serif;
font-family: 'Open Sans Condensed', sans-serif;
font-family: 'Open Sans', sans-serif;
*/


/***** Main Body *****/

body,
html {
    font-family: 'Open Sans', Arial, sans-serif;
    height:100%;
    color: @dark;
}
body{
    position: relative;
    top: 50px; /* to compensate for header */
}
/***** Contenr *****/
.content{
    width: 100%;
    padding-left: 250px;
}
.content-container > section {
    height: 100vh;
}

/***** Headings *****/

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: 'Lato', 'Open Sans', Arial, sans-serif;
}

h1,
h2,
h3 {
    font-weight: 300;
}


/***** Font Awesome *****/

.fa-2x {
    font-size: 2em;
}

.fa {
    position: relative;
    display: table-cell;
    width: 40px;
    text-align: center;
    vertical-align: middle;
    font-size: 20px;
}

/***** Links *****/
a:hover,
a:focus {
    text-decoration: none;
}


/***** Main Menu *****/
.navbar.navbar-custom{
    margin-bottom: 0;
}
.navbar-custom {
    background-color: @dark;
    border-right: 1px solid #333;
    width: 250px;
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    border-radius: 0;
}

.navbar-brand, h3{
    color: @teal;
    text-align: center; 
}
.navbar-header, .navbar-brand{
    float:none;
    height:auto;
    margin: 0 auto;
}
.navbar-brand img{
    max-width: 70px;
}
.navbar-custom li{
    display: block;
    height: 60px;
    width: 100%;
    margin-left: 20px;
}
.navbar-custom li>a,
.navbar-custom li>a:visited{
    color: @lightgray;
    position:relative;
    display:table;
    border-collapse:collapse;
    border-spacing:0;
    text-decoration:none;
    -webkit-transform:translateZ(0) scale(1,1);
    -webkit-transition:all .1s linear;
    transition:all .1s linear;
}

.navbar-custom li:hover> a,
.navbar-custom li.active > a {
    color: @teal;
    background-color: @dark;
}
.navbar-custom .nav-text {
position:relative;
display:table-cell;
vertical-align:middle;
}

.navbar-custom .nav-icon {
    position:relative;
    display:table-cell;
    width:60px;
    height:36px;
    text-align:center;
    vertical-align:middle;
}

/***** Footer *****/
footer{
    height: 100vh;
    width: 100vw;
}

.footer-callout{
    /*background-color: @peach;*/
    color: @light;
    padding: 50px;
}



